:root {
  --bg-light: rgb(248, 248, 248);
  --bg-dark: rgb(16, 29, 71);
  --color-bg-navbar: #1e2b3b;
  --color-text-navbar: rgb(250, 248, 248);
  --height-navbar: 46px;
}

body {
  margin: 0;
  background: var(--color-bg-navbar);
}

.navbar {
  position: fixed;
  top: 0;
  background-color: var(--color-bg-navbar);
  width: 100%;
  display: flex;
  align-items: center;
  padding: 7px;
  flex-direction: row;
  height: var(--height-navbar);
  border-bottom: 2px solid rgb(53, 101, 141);
  box-shadow: 1px 2px 3px 1px #213955;
  z-index: 2;
}

.menu__bars {
  width: 20px;
  height: 20px;
  padding: 10px;
  cursor: pointer;
}

.menu__bars svg {
  fill: var(--color-text-navbar);
}

.title__logo__navbar {
  font-size: 26px;
  text-decoration: none;
  color: var(--color-text-navbar);
  /* margin-top: -4px; */
  font-family: 'Times New Roman', Times, serif;
}

.title__logo__navbar:hover {
  opacity: 0.8;
}

.search__icon {
  width: 16px;
  display: flex;
  align-items: center;
  color: var(--color-text-navbar);
  max-height: 100%;
  padding: 10px;
  background-color: #284e7c;
  cursor: pointer;
}

.search__icon:hover {
  background-color: #1e416b;
}

.search__icon svg {
  fill: var(--color-text-navbar);
}
.form__search {
  display: flex;
  align-items: center;
  margin-left: calc(100% - 450px);
}

.input__search {
  padding: 10px 7px;
  width: 200px;
  background-color: #222222;
  outline: none;
  border: none;
  border-bottom: 1px solid #d1d0d0;
  color: whitesmoke;
  max-width: calc(100% - 50px);
}

.input__search::placeholder {
  color: rgb(170, 168, 168);
}

.input__search:focus {
  border-bottom: 1px solid #9cc9ff;
}

.sidebar {
  top: calc(var(--height-navbar) + 13px);
  position: fixed;
  max-width: 350px;
  overflow: auto;
  display: flex;
  flex-direction: column;
  max-height: calc(100% - calc(var(--height-navbar) + 13px) - 22px);
  z-index: 2;
}

.genres__extend__button {
  cursor: pointer;
}

.content__bar__menu {
  display: flex;
  align-items: center;
}

.sidebar__content {
  padding: 10px;
  background-color: var(--color-bg-navbar);
  width: 250px;
  height: 100vh;
  border-right: 2px solid rgb(53, 101, 141);
  box-shadow: 1px 2px 3px 1px #213955;
  border-top: 1px solid rgba(197, 196, 196, 0.2);
}

.sidebar .indicator__navegation {
