  padding: 10px;
  font-weight: lighter;
  letter-spacing: 0.5px;
  border-bottom: 1px solid #9cc9ff;
  margin-bottom: 10px;
  font-family: ui-serif;
}

.hidden {
  display:none;
}
.show{
  display:block;
}

.list__menu__options {
  display: flex;
  flex-direction: column;
}

