  /* height: 10000px; */
}

.sidebar::-webkit-scrollbar,
.description__video::-webkit-scrollbar {
  width: 8px;
  height: 3px;
}

.options__stream::-webkit-scrollbar,
.seasons__stream::-webkit-scrollbar {
  width: 10px;
  height: 12px;
}
.sidebar::-webkit-scrollbar-track,
.description__video::-webkit-scrollbar-track,
.options__stream::-webkit-scrollbar-track,
