  background-color: #bbb7b7;
}
.sidebar::-webkit-scrollbar-track-piece,
.description__video::-webkit-scrollbar-track-piece,
.options__stream::-webkit-scrollbar-track-piece,
.seasons__stream::-webkit-scrollbar-track-piece {
