}
.sidebar::-webkit-scrollbar-thumb,
.description__video::-webkit-scrollbar-thumb,
.options__stream::-webkit-scrollbar-thumb,
.seasons__stream::-webkit-scrollbar-thumb {
  height: 50px;
  background-color: rgb(214, 212, 212);
  border-radius: 3px;
}
.sidebar::-webkit-scrollbar-corner,
.description__video::-webkit-scrollbar-corner,
.options__stream::-webkit-scrollbar-corner,
.seasons__stream::-webkit-scrollbar-corner {
  background-color: #646464;
}
.sidebar::-webkit-resizer,
.description__video::-webkit-resizer,
.options__stream::-webkit-scrollbar-thumb,
.seasons__stream::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgb(0 0 0 / 10%);
  background-image: -webkit-linear-gradient(bottom, #30cfd0 0%, #330867 100%);
  background-image: linear-gradient(to top, #30cfd0 0%, #330867 100%);
}

