.tab {
  overflow: hidden;
  background-color: transparent;
  border-bottom: 2px solid #fff;
}

/* Style the buttons inside the tab */
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
  font-size: 16px;
  color: white;
  background-color: transparent;
  font-family: Roboto, -apple-system, system-ui, BlinkMacSystemFont, segoe ui,
    helvetica neue, Arial, sans-serif;

  margin: 0 !important;
}

.tab .tabload {
  font-size: 14px;
  text-transform: uppercase;
  color: rgb(194, 194, 194) !important;
}

.tab .tabload:hover {
  background-color: #314152 !important;
  /* border-bottom: 3px solid #314152; */
  color: white !important;
}

/* Create an active/current tablink class */
.tab .tabload.active {
  background-color: #334458 !important;
  border-bottom: 3px solid #04a1f4 !important;
  color: var(--color-text-navbar) !important;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #04a1f4;
}

/* Create an active/current tablink class */
.tab button.active {
  background-color: #1c2836;
  border-bottom: 6px solid #04a1f4;
  color: var(--color-text-navbar) !important;
}

.btnMoreVideos {
  background-color: rgb(66, 103, 223);
  font-weight: 450;
  padding: 13px 25px;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  border-radius: 2px;
  border: none;
  outline: none;
  text-align: center;
  text-decoration: none;
  font-family: system-ui;
}

.background__loading {
  animation-duration: 7s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: preloadAnimation;
  animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(to right, #0b1c31 18%, #0b1b30 18%, #0c1f38 53%);
  display: flex;
  min-height: 100px;
  width: 154px;
  height: 345px;
  flex-direction: column;
  margin: 10px;
}

@keyframes preloadAnimation {
  0% {
    background-position: -769px 0;
  }
  100% {
    background-position: 769px 0;
  }
}

