/* SKELETOR CSS */
.skeletor-video .background-image {
  height: 231px;
  width: 154px;
  background: #656871;
  position: relative;
  overflow: hidden;
}
.skeletor-img {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.skeletor-info {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
}
.skeletor-info .line {
  background: #656871;
  border-radius: 10px;
  height: 13px;
  margin: 10px 0;
  overflow: hidden;
  position: relative;
}
.skeletor-info .line-1 {
  width: calc(100% - 15%);
}
.skeletor-info .line-3 {
  width: calc(100% - 40%);
}
.skeletor-video .background-image::before,
.skeletor-info .line::before,
.skeletor-watch::before{
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background-image: linear-gradient(to right, #656871 0%, #888b94 20%, #656871 40%, #656871 100%);
  background-repeat: no-repeat;
  background-size: 450px 400px;
  animation: shimmer 1s linear infinite;
}
.skeletor-video {
  padding: 40px;
  display: flex;
  align-items: center;
}
.skeletor-load-video {
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100%;
  background: #1e2b3b;
}
.skeletor-video .background-image::before,
.skeletor-watch::before{
  background-size: 650px 600px;
}
.skeletor-stream-video {
  max-width: calc(100% - 40px);
  padding: 20px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 35px;
}

.skeletor-container-watch {
  height: 450px;
  max-width: 700px;
  width: calc(100% - 20px);
  display: flex;
  align-items: center;
  justify-content: center;
}
.skeletor-watch {
  background: #656871;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}
@keyframes shimmer {
  0%{
    background-position: -450px 0;
  }
  100%{
    background-position: 450px 0;
 }
}
